<template>
  <div>
    <div>
      <input v-model="inputValue"/>
      <button @click="addOneThing">新增</button>
    </div>
    <ul>
      <todo-item 
        v-for="(item, index) of list"
        :key="index"
        :content="item"
        :index="index"
        @delete="handleDelete"
      ></todo-item>
    </ul>
  </div>
</template>

<script>
import TodoItem from './components/TodoItem.vue'

export default {
  components: {
    'todo-item': TodoItem
  },
  data () {
    return {
      inputValue: "",
      list: [],
    }
  },
  methods: {
    addOneThing () {
      this.list.push(this.inputValue)
      this.inputValue = ""
    },
    handleDelete (index) {
      this.list.splice(index, 1);
    }
  },
}
</script>

